<template>
  <div>
    <body class="bg-gray-200">
    <main class="main-content  mt-0">
      <div class="page-header align-items-start min-vh-100" :style="{'background-image': 'url(' + background + ')'}">
        <span class="mask bg-gradient-dark opacity-6"></span>
        <div class="container my-auto">
          <h1 class="text-center pb-5">User Login</h1>
          <div class="row">
            <div class="col-lg-4 col-md-8 col-12 mx-auto">
              <div class="card z-index-0 fadeIn3 fadeInBottom">
                <div class="card-header p-0 position-relative mt-n4 mx-3 z-index-2">
                  <div class="bg-gradient-primary shadow-primary border-radius-lg py-3 pe-1">
                    <h4 class="text-white font-weight-bolder text-center mt-2 mb-0">Sign in</h4>
                    <div class="row mt-3">
                      <div class="col-2 text-center ms-auto">
                        <a class="btn btn-link px-3" href="javascript:;">
                          <i class="fa fa-facebook text-white text-lg"></i>
                        </a>
                      </div>
                      <div class="col-2 text-center px-1">
                        <a class="btn btn-link px-3" href="javascript:;">
                          <i class="fa fa-github text-white text-lg"></i>
                        </a>
                      </div>
                      <div class="col-2 text-center me-auto">
                        <a class="btn btn-link px-3" href="javascript:;">
                          <i class="fa fa-google text-white text-lg"></i>
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="card-body">
                  <form role="form" class="text-start">
                    <div class="input-group input-group-outline my-3">
                      <input type="email" v-model="email" class="form-control" placeholder="Email">
                    </div>
                    <div class="input-group input-group-outline mb-3">
                      <input type="password" v-model="password" class="form-control" placeholder="Password">
                    </div>
                    <div class="text-center">
                      <button type="button" @click="Login" class="btn bg-gradient-primary w-100 my-4 mb-2">Sign in</button>
                    </div>
                    <p class="mt-4 text-sm text-center">
                      Don't have an account?
                      <router-link :to="{name: 'Register'}">
                        Sign up
                      </router-link>
                    </p>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
    </body>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "User.Login",
  data() {
    return {
      background: 'public/assets/img/backgound/fb.jpg',
      email: '',
      password: ''
    }
  },
  methods: {
    Login () {
      axios.post('/login', {"email": this.email, "password": this.password})
      .then(response => {
        if (response.data.data) {
          this.$notify({text: "Login Successful", type: 'success'})
          this.$store.commit('LOGIN_CLIENT', response.data.data)
          this.$router.push({ name: 'User.Dashboard' })
        }
      })
    }
  }
}
</script>